svg{
    color: white;
    font-size: 1.5rem;
}

.icon-container{
    display: flex;
}

nav{
    a:not(.skip-link){
        color: white;
    }

    .skip-link{
        color: blue;
        position: absolute;
        left: -10000px;
        top: auto;
        width: 1px;
        height: 1px;
        overflow: hidden;
        background-color: white;

        &:focus{
            width: auto;
            height: auto;
            left: auto;
        }
    }

    .icon-container{
        display: flex;

        svg{
            color: white;
            font-size: 1.5rem;
        }
    }
}